import React from 'react';
import styled from 'styled-components';
import * as Mixins from '../Mixins';
import * as t from '../Typography';
import Layout, { Content } from '../components/Layout';
import Placeholder from '../images/placeholder.png';
import Okkake from '../images/okkake.png';
import Robes from '../images/robes.png';
import Ryde from '../images/ryde.png';
import { HireMe, LinkButton } from '../components/Button.js';
import HireMePopup from '../components/HireMePopup.js';
import { media } from '../MediaQueries';
import Colors from '../Colors';
import Img from 'gatsby-image';
import { graphql } from 'gatsby';
import { darken } from 'polished';

const AboveFold = styled.div`
  ${Mixins.aboveFoldMixin}
  padding: 140px 0 60px 0;
  ${t.H1} {
    color: ${Colors.darkest};
  }
`;

const Block = styled.div`
  &:nth-child(odd) {
    border: solid 1px ${darken(0.1, Colors.light)};
    background-color: ${Colors.light};
  }
`;

const BlockContent = styled(Content)`
  ${Mixins.block}
  padding: 100px 40px;
  ${media.tablet`
    flex-direction: column;
    align-items: baseline;
  `};
  ${media.phone`
    padding: 40px 10px;
  `};
  ${t.P} {
    margin-top: 10px;
  }
  ${t.H2} {
    margin-top: 0;
  }
  img {
    width: 100%;
    height: auto;
  }
`;

const DivWrapper = styled.div`
  padding: 80px 30px;
  ${media.tablet`padding: 50px 0;`}
  &:first-child {
    ${media.tablet`
      margin-bottom: 40px;
  `};
  }
`;

const ItemImage = styled.img`
  max-width: 85%;
  position: relative;
  ${media.tablet`max-width: none;`}
`;

const HomepageWrapper = styled.div`
  ${Mixins.wrapper}
  .who-desc {
    display: block;
    margin: 0 auto 60px auto;
    max-width: 90%;
  }
  ${t.LargeP} {
    margin-bottom: 28px;
  }
  ${t.H1} {
    margin: 0 0 20px 0;
  }
  .avatar {
    max-width: 200px;
    width: 80%;
    margin: 0 auto 50px auto;
    border-radius: 50%;
    display: block;
    ${media.tablet`max-width: 70%;`}
  }
  .link {
    padding: 0;
    color: ${Colors.darkest};
    text-decoration: underlined;
    svg {
      margin-left: 7px;
    }
  }
  .portfolio {
    margin: 100px 0 50px 0;
    font-size: 42px;
  }
`;

const WorkWithMe = styled.div`
  padding: 80px;
  width: 73%;
  border-radius: 6px;
  box-shadow: 0 2px 26px 0 rgba(57, 55, 55, 0.08);
  background-color: #ffffff;
  text-align: center;
  position: relative;
  margin: 100px auto -150px auto;
  ${t.LargeP} {
    max-width: 80%;
    margin: 0 auto 28px auto;
  }
  ${media.tablet`
    width: auto;
    padding: 40px;
    margin: 50px 30px -100px 30px;
  `};
`;

class Homepage extends React.Component {
  state = {
    openHireMePopup: false
  };

  handleRequestDemoClose = () => {
    this.setState({
      openHireMePopup: false
    });
  };

  openContactPopup = () => {
    this.setState({
      openHireMePopup: true
    });
  };

  render() {
    const { openHireMePopup } = this.state;
    const { data } = this.props;
    return (
      <HomepageWrapper>
        <Layout theme="white" bigFooter openContactPopup={this.openContactPopup}>
          <AboveFold>
            <Img fluid={data.avatarHomepage.childImageSharp.fluid} alt="Apollo880" className="avatar" />
            <t.H1 primary align="center">
              Apollo880
            </t.H1>
            <t.LargeP align="center" max45>
              Web engineer
            </t.LargeP>
            <HireMe large onClick={this.openContactPopup} book>
              Hire me
            </HireMe>
          </AboveFold>
          <Content>
            <t.H2 primary align="center" bold>
              Skills
            </t.H2>
            <Content align="center" style={{maxWidth: 800, border: '1px solid #aaa'}}>
              <t.P align="left" max70 style={{marginTop: 20}}>
                【Web】Rails + mysql(postgres) + unicorn(puma) + NginX
              </t.P>
              <t.P align="left" max70 style={{marginTop: 20}}>
                【iOS/Android】react native + typeScript + redux
              </t.P>
              <t.P align="left" max70 style={{marginTop: 20, marginBottom: 20}}>
                【AWS】 EC2, RDS, ALB, S3, Lambda, ElastiCache, Athena, Cloudwatch
              </t.P>
              <t.P align="left" max70 style={{marginTop: 20, marginBottom: 20}}>
                【Other】 terraform, capistrano, itamae, docker
              </t.P>
            </Content>
            <t.H2 primary align="center" bold className="portfolio">
              Portfolio
            </t.H2>
          </Content>
          <Block>
            <BlockContent>
              <DivWrapper>
                <t.H2 bold>服のサブスクリプションサービス「Robes」</t.H2>
                <t.P>メンズファッションのサブスクリプションサービス。</t.P>
                <t.P>Firebase上で管理されていたシステムをAWS（+ Rails, Vue.js）に全面移行する業務を行なっておりました。要件定義や設計、インフラ構築を主に担当。</t.P>
                <LinkButton primary bold className="link" as="a"
                  target="_blank"
                  href="https://robes.jp/">
                  https://robes.jp/
                </LinkButton>
              </DivWrapper>
              <DivWrapper>
                <ItemImage src={Robes} alt="Placeholder title" />
              </DivWrapper>
            </BlockContent>
          </Block>
          <Block>
            <BlockContent>
              <DivWrapper>
                <ItemImage src={Ryde} alt="Placeholder title" />
              </DivWrapper>
              <DivWrapper>
                <t.H2 bold>位置情報サービス「RYDE」</t.H2>
                <t.P>シェアサイクル 、バス、タクシーなどの交通手段の検索・利用を簡単にするモビリティプラットフォーム開発プロジェクト。</t.P>
                <t.P>インフラ構築から、React nativeによるiOS・Android開発まで携わる。</t.P>
                <LinkButton primary bold className="link" as="a"
                  target="_blank"
                  href="https://ryde-go.com/">
                  https://ryde-go.com/
                </LinkButton>
              </DivWrapper>
            </BlockContent>
          </Block>
          <Block>
            <BlockContent>
              <DivWrapper>
                <ItemImage src={Okkake} alt="okkake" />
              </DivWrapper>
              <DivWrapper>
                <t.H2 bold>声優情報サービス「オッカケ!!」</t.H2>
                <t.P>声優をフォローすることで最新ニュース、出演情報などをチェックすることができるWEB、Android、iOSのサービス。</t.P>
                <t.P>サーバーサイド（Rails）・フロントエンド（React）と広く業務を担当。</t.P>
                <LinkButton primary bold className="link" as="a"
                  target="_blank"
                  href="https://okkake.me/">
                  https://okkake.me/
                </LinkButton>
              </DivWrapper>
            </BlockContent>
          </Block>
          <WorkWithMe>
            <t.H1 green>Get in touch with me</t.H1>
            <t.LargeP>
              Fancy working with me? Contact me for more info!{' '}
            </t.LargeP>
            <HireMe onClick={this.openContactPopup} book>
              Contact me
            </HireMe>
          </WorkWithMe>
        </Layout>
        <HireMePopup open={openHireMePopup} handleClose={this.handleRequestDemoClose} />
      </HomepageWrapper>
    );
  }
}

export default Homepage;

export const pageQuery = graphql`
  query {
    avatarHomepage: file(relativePath: { eq: "avatar.jpg" }) {
      ...fluidImage
    }
  }
`;
